/*start style elemnts*/
body {
	font-family: Rubik, sans-serif;
	background: #83a4d4; /* fallback for old browsers */
	background: -webkit-linear-gradient(
		to left,
		#b6fbff,
		#83a4d4
	); /* Chrome 10-25, Safari 5.1-6 */
	background: linear-gradient(
		to left,
		#b6fbff,
		#83a4d4
	); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

button:disabled {
	cursor: not-allowed;
}

footer {
	color: #f1e9e5;
}
/*end*/

/*start custom Clssess */

.main-title {
	color: #57837b;
}

.fit-content {
	width: fit-content;
}
/*end*/

/*start style calculator */
.calculator {
	background-color: #eeeeee;
	border: 5px solid #515e63;
}
/*end*/

/*start style calculator display*/
.calculator__display {
	font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande',
		'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
	background-color: #082032;
	/* color: #eeeeee; */
	color: white;
}

/* width */
.calculator__display::-webkit-scrollbar {
	width: 10px;
	height: 8px;
}

/* Track */
.calculator__display::-webkit-scrollbar-track {
	background: #f1f1f1;
	/* border-radius: 16px; */
}

/* Handle */
.calculator__display::-webkit-scrollbar-thumb {
	background: #888;
	/* border-radius: 16px; */
}

/* Handle on hover */
.calculator__display::-webkit-scrollbar-thumb:hover {
	background: #555;
}
/*end*/

/*start style operator buttons */
.btn-operator {
	color: white;
	background-color: #666;
}

.btn-operator:hover {
	filter: brightness(110%);
	color: white;
}
/*end*/

/*start style operator buttons */
.btn-number {
	color: #333;
	background-color: #aaa;
	transition: all 0.2s ease-in-out;
}

.btn-number:hover {
	filter: brightness(110%);
}
/*end*/

/*start style clear buttons */
.btn-clear {
	color: white;
	background-color: #f8485e;
	transition: all 0.2s ease-in-out;
}

.btn-clear:hover {
	filter: brightness(90%);
	color: white;
}
/*end*/

/*start style equal buttons */
.btn-equal {
	color: white;
	background-color: #00c1d4;
}

.btn-equal:hover {
	filter: brightness(90%);
	color: white;
}
/*end*/

/*start style all buttons */
.btn:active {
	transform: translateY(2px);
}

.btn:focus {
	outline: none;
	border: none;
	box-shadow: none;
}
/*end*/
